/**
 * MVU
 * @param enemyName 
 * @returns MVUnull
 */
export function getEnemyMvuData(enemyName: string): EnemyMvuData | null {
  return ENEMY_DATABASE[enemyName] || null;
}

/**
 * 
 * @param enemyName 
 * @returns 
 */
export function hasEnemyData(enemyName: string): boolean {
  return enemyName in ENEMY_DATABASE;
}
