import type { Character, Skill, Item, CombatLogEntry } from './types';

// 
const createLog = (msg: string, source: string, type: CombatLogEntry['type'] = 'info'): CombatLogEntry => ({
  id: Math.random().toString(36).substr(2, 9),
  turn: 0,
  message: msg,
  source,
  type
});

// ---  ---
export const PLAYER_SKILLS: Skill[] = [
  {
    id: 's1',
    name: '',
    description: '',
    cost: 5,
    type: 'attack',
    cooldown: 0,
    currentCooldown: 0,
    effect: (user, target) => {
      const dmg = Math.floor(user.stats.charm * 1.5 + user.stats.sexPower * 0.5);
      target.stats.currentPleasure += dmg;
      return createLog(`${user.name}  [] ${dmg} `, 'player', 'damage');
    }
  },
  {
    id: 's2',
    name: '',
    description: '',
    cost: 15,
    type: 'buff',
    cooldown: 3,
    currentCooldown: 0,
    effect: (user, _target) => {
      return createLog(`${user.name}  []`, 'player', 'info');
    }
  },
  {
    id: 's3',
    name: '',
    description: '',
    cost: 40,
    type: 'ultimate',
    cooldown: 5,
    currentCooldown: 0,
    effect: (user, target) => {
      const dmg = Math.floor(user.stats.sexPower * 3.5);
      target.stats.currentPleasure += dmg;
      return createLog(`${user.name}  [] ${dmg} `, 'player', 'critical');
    }
  }
];

