// ---  ---
export const PLAYER_ITEMS: Item[] = [
  {
    id: 'i1',
    name: '',
    description: ' 30 ',
    quantity: 3,
    staminaRestore: 30,
    effect: (user, _target) => {
      user.stats.currentEndurance = Math.min(user.stats.maxEndurance, user.stats.currentEndurance + 30);
      return createLog(`${user.name}  []`, 'player', 'heal');
    }
  },
  {
    id: 'i2',
    name: '',
    description: ' 20 ',
    quantity: 2,
    pleasureReduce: 20,
    effect: (user, _target) => {
      user.stats.currentPleasure = Math.max(0, user.stats.currentPleasure - 20);
      return createLog(`${user.name}  []`, 'player', 'heal');
    }
  }
];

// ---  ---
export function createDefaultPlayer(): Character {
  return {
    id: 'player',
    name: '',
    avatarUrl: 'https://picsum.photos/400/600?random=1',
    isPlayer: true,
    statusEffects: [],
    items: PLAYER_ITEMS.map(i => ({ ...i })),
    skills: PLAYER_SKILLS.map(s => ({ ...s })),
    stats: {
      maxEndurance: 100,
      currentEndurance: 100,
      maxPleasure: 100,
      currentPleasure: 0,
      willpower: 100,
      baseWillpower: 100,
      climaxCount: 0,
      maxClimaxCount: 3,
      sexPower: 25,
      baseEndurance: 15,
      evasion: 10,
      crit: 5,
      charm: 30,
      luck: 15,
    }
  };
}

export function createDefaultEnemy(): Character {
  return {
    id: 'enemy',
    name: '',
    avatarUrl: 'https://picsum.photos/400/600?random=2',
    isPlayer: false,
    statusEffects: [],
    items: [],
    skills: ENEMY_SKILLS.map(s => ({ ...s })),
    stats: {
      maxEndurance: 150,
      currentEndurance: 150,
      maxPleasure: 100,
      currentPleasure: 0,
      willpower: 80,
      baseWillpower: 80,
      climaxCount: 0,
      maxClimaxCount: 3,
      sexPower: 20,
      baseEndurance: 20,
      evasion: 5,
      crit: 10,
      charm: 10,
      luck: 5,
    }
  };
}

