                        }
                    }
                    else {
                        // 
                        if (isSelf) {
                            // 
                            bonusPath = '..';
                            targetName = enemy.value.name;
                        }
                        else {
                            // 
                            bonusPath = '.';
                            targetName = player.value.name;
                        }
                    }
                    // UI
                    // key_ID_____
                    // 
                    const sign = isPositive ? 'pos' : 'neg';
                    const statusKey = `${effectType}_${skillId}_${effectName}_${actualValue}_${sign}_${isPercentage ? 'pct' : 'fix'}_${Date.now()}`;
                    const statusPath = isPlayerSkill
                        ? isSelf
                            ? '.'
                            : '..'
                        : isSelf
                            ? '..'
                            : '.';
                    const statusList = _.get(mvuData.stat_data, statusPath, {});
                    // ID
                    // 
                    // IDeffectNameeffectName
                    const existingStatusKey = Object.keys(statusList).find(key => {
                        const keyParts = key.split('_');
                        // key_ID_____
                        // _ID____
                        // ID
                        if (keyParts.length < 2)
                            return false;
                        return keyParts[0] === effectType && keyParts[1] === skillId;
                    });
                    if (existingStatusKey) {
                        // 
                        const oldDuration = statusList[existingStatusKey];
                        statusList[existingStatusKey] = duration;
                        _.set(mvuData.stat_data, statusPath, statusList);
                        console.info(`[] : ${existingStatusKey} (${oldDuration} -> ${duration})`);
                        logs.push(`${targetName}  ${effectType}  ( ${duration} )`);
                        continue; // 
                    }
                    console.info(`[] : ${effectType}, ID: ${skillId}, : ${isPositive ? '+' : '-'}${actualValue}`);
                    // 
                    const currentBonus = _.get(mvuData.stat_data, `${bonusPath}.${bonusField}`, 0);
                    const newBonus = currentBonus + (isPositive ? actualValue : -actualValue);
                    // 
                    _.set(mvuData.stat_data, `${bonusPath}.${bonusField}`, newBonus);
                    // 
                    statusList[statusKey] = duration;
                    _.set(mvuData.stat_data, statusPath, statusList);
                    // 
                    const effectTypeName = {
                        : '',
                        : '',
                        : '',
                        : '',
                        : '',
                        : '',
                        : '',
                    };
                    const typeName = effectTypeName[effectType] || effectType;
                    const valueText = isPercentage ? `${actualValue}%` : `${actualValue}`;
                    const signSymbol = isPositive ? '+' : '-';
                    logs.push(`${targetName} ${signSymbol}${valueText} ${typeName} ( ${duration} )`);
                }
                // MVU
                await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
            }
            catch (e) {
                console.error('[] ', e);
                logs.push('');
            }
            return logs;
        }
        // MVU
        async function updateStatusEffectsFromMvu() {
            const logs = [];
            try {
                if (typeof Mvu === 'undefined') {
                    return logs;
                }
                const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                if (!mvuData) {
                    return logs;
                }
                // 
                const playerStatusList = _.get(mvuData.stat_data, '.', {});
                const updatedPlayerStatus = {};
                for (const [key, duration] of Object.entries(playerStatusList)) {
                    const newDuration = duration - 1;
                    if (newDuration > 0) {
                        updatedPlayerStatus[key] = newDuration;
                    }
                    else {
                        // 
                        // key_ID_____
                        // _ID____
                        clearBonusFromStatus(key, '.', mvuData);
                        const effectType = key.split('_')[0]; // 
                        logs.push(`${player.value.name}  ${getEffectTypeName(effectType)} `);
                    }
                }
                _.set(mvuData.stat_data, '.', updatedPlayerStatus);
                // 
                const enemyStatusList = _.get(mvuData.stat_data, '..', {});
                const updatedEnemyStatus = {};
                for (const [key, duration] of Object.entries(enemyStatusList)) {
                    const newDuration = duration - 1;
                    if (newDuration > 0) {
                        updatedEnemyStatus[key] = newDuration;
                    }
                    else {
                        // 
                        // key_ID_____
                        // _ID____
                        clearBonusFromStatus(key, '..', mvuData);
                        const effectType = key.split('_')[0]; // 
                        logs.push(`${enemy.value.name}  ${getEffectTypeName(effectType)} `);
                    }
                }
                _.set(mvuData.stat_data, '..', updatedEnemyStatus);
                // 
                if (playerBoundTurns.value > 0) {
                    playerBoundTurns.value--;
                    if (playerBoundTurns.value === 0) {
                        logs.push(`${player.value.name} `);
                    }
                }
                if (enemyBoundTurns.value > 0) {
                    enemyBoundTurns.value--;
                    if (enemyBoundTurns.value === 0) {
                        logs.push(`${enemy.value.name} `);
                    }
                }
                // MVU
                await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
            }
            catch (e) {
                console.error('[] ', e);
            }
            return logs;
        }
        // 
        // key
        // statusKey_ID_____
        // _ID____
        function clearBonusFromStatus(statusKey, bonusPath, mvuData) {
            try {
                // key
                const parts = statusKey.split('_');
                // 
                let effectType, effectValue, isPositive, isPercentage;
                if (parts.length >= 6) {
                    // _ID_____
                    effectType = parts[0];
                    effectValue = parseFloat(parts[3]) || 0;
                    const sign = parts[4];
                    isPositive = sign === 'pos';
                    isPercentage = parts[5] === 'pct';
                }
                else if (parts.length >= 5) {
                    // _ID____
                    effectType = parts[0];
                    effectValue = parseFloat(parts[3]) || 0;
                    isPercentage = parts[4] === 'pct';
                    // 
                    const currentBonus = _.get(mvuData.stat_data, `${bonusPath}.${effectType === '' ? (isPercentage ? '' : '') : (effectType === '' ? (isPercentage ? '' : '') : '')}`, 0);
                    isPositive = currentBonus >= 0; // 
                    console.warn('[] key:', statusKey);
                }
                else {
                    console.warn('[] key:', statusKey);
                    return;
                }
                if (effectValue === 0) {
                    return; // 
                }
                // 
                let bonusField;
                if (effectType === '') {
                    bonusField = isPercentage ? '' : '';
                }
                else if (effectType === '') {
                    bonusField = isPercentage ? '' : '';
                }
                else {
                    const bonusFieldMap = {
                        : '',
                        : '',
                        : '',
                        : '',
                        : '',
                    };
                    bonusField = bonusFieldMap[effectType];
                }
                if (!bonusField) {
                    console.warn(`[] : ${effectType}`);
                    return;
                }
                // 
                // newBonus = currentBonus + (isPositive ? actualValue : -actualValue)
                // newBonus = currentBonus - (isPositive ? actualValue : -actualValue)
                const currentBonus = _.get(mvuData.stat_data, `${bonusPath}.${bonusField}`, 0);
                const newBonus = currentBonus - (isPositive ? effectValue : -effectValue);
                _.set(mvuData.stat_data, `${bonusPath}.${bonusField}`, newBonus);
                console.info(`[] ${effectType}: ${currentBonus} -> ${newBonus} (${isPositive ? '' : ''}${effectValue})`);
            }
            catch (e) {
                console.error('[] ', e);
            }
        }
        // 
        function getEffectTypeName(effectType) {
            const names = {
                : '',
                : '',
                : '',
                : '',
                : '',
                : '',
                : '',
                : '',
            };
            return names[effectType] || effectType;
        }
        // MVUUI
        async function reloadStatusFromMvu() {
            try {
                if (typeof Mvu === 'undefined') {
                    return;
                }
                const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                if (!mvuData) {
                    return;
                }
                const data = mvuData.stat_data;
                // 
                const playerTempBonus = _.get(data, '.', {});
                const playerPermBonus = _.get(data, '.', {});
                const playerEquipBonus = _.get(data, '.', {});
                // 
                const enemyTempBonus = _.get(data, '..', {});
                //  +  +  + 
                // 
                // 
                player.value.stats.sexPower = _.get(data, '.', 25);
                player.value.stats.baseEndurance = _.get(data, '.', 15);
                // 
                const playerBaseCharm = _.get(data, '.$', 10);
                player.value.stats.charm =
                    playerBaseCharm +
                        _.get(playerTempBonus, '', 0) +
                        _.get(playerPermBonus, '', 0) +
                        _.get(playerEquipBonus, '', 0);
                const playerBaseLuck = _.get(data, '.$', 10);
                player.value.stats.luck =
                    playerBaseLuck +
                        _.get(playerTempBonus, '', 0) +
                        _.get(playerPermBonus, '', 0) +
                        _.get(playerEquipBonus, '', 0);
                const playerBaseEvasion = _.get(data, '.$', 0);
                player.value.stats.evasion =
                    playerBaseEvasion +
                        _.get(playerTempBonus, '', 0) +
                        _.get(playerPermBonus, '', 0) +
                        _.get(playerEquipBonus, '', 0);
                const playerBaseCrit = _.get(data, '.$', 0);
                player.value.stats.crit = Math.min(100, Math.max(0, playerBaseCrit +
                    _.get(playerTempBonus, '', 0) +
                    _.get(playerPermBonus, '', 0) +
                    _.get(playerEquipBonus, '', 0)));
                // 
                const enemyBaseSexPower = _.get(data, '.', 0);
                const enemySexPowerBonus = _.get(enemyTempBonus, '', 0);
                const enemySexPowerMultiplier = _.get(enemyTempBonus, '', 0);
                enemy.value.stats.sexPower =
                    enemyBaseSexPower +
                        enemySexPowerBonus +
                        Math.floor(((enemyBaseSexPower + enemySexPowerBonus) * enemySexPowerMultiplier) / 100);
                const enemyBaseEndurance = _.get(data, '.', 0);
                const enemyEnduranceBonus = _.get(enemyTempBonus, '', 0);
                const enemyEnduranceMultiplier = _.get(enemyTempBonus, '', 0);
                enemy.value.stats.baseEndurance =
                    enemyBaseEndurance +
                        enemyEnduranceBonus +
                        Math.floor(((enemyBaseEndurance + enemyEnduranceBonus) * enemyEnduranceMultiplier) / 100);
                enemy.value.stats.charm = _.get(data, '.', 0) + _.get(enemyTempBonus, '', 0);
                enemy.value.stats.luck = _.get(data, '.', 0) + _.get(enemyTempBonus, '', 0);
                enemy.value.stats.evasion = _.get(data, '.', 0) + _.get(enemyTempBonus, '', 0);
                enemy.value.stats.crit = Math.min(100, Math.max(0, _.get(data, '.', 0) + _.get(enemyTempBonus, '', 0)));
            }
            catch (e) {
                console.error('[] ', e);
            }
        }
