        function addLog(message, source, type = 'info') {
            const logEntry = {
                id: Math.random().toString(36).substr(2, 9),
                turn: turnState.currentTurn,
                message,
                source,
                type,
            };
            logs.value.push(logEntry);
            // MVUUI
            (async () => {
                try {
                    if (typeof Mvu !== 'undefined') {
                        const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                        if (mvuData) {
                            const logKey = `${turnState.currentTurn}_${Date.now()}`;
                            const logValue = `[${logEntry.turn}] ${logEntry.source === 'player' ? player.value.name : logEntry.source === 'enemy' ? enemy.value.name : ''}: ${logEntry.message}`;
                            const currentLogs = _.get(mvuData.stat_data, '.', {});
                            const updatedLogs = { ...currentLogs, [logKey]: logValue };
                            _.set(mvuData.stat_data, '.', updatedLogs);
                            // UI
                            await Mvu.replaceMvuData(mvuData, { type: 'message', message_id: 'latest' });
                        }
                    }
                }
                catch (e) {
                    console.warn('[] ', e);
                }
            })();
        }
        function cloneCharacter(char) {
            return {
                ...char,
                stats: { ...char.stats },
                skills: char.skills.map(s => ({ ...s })),
                items: char.items.map(i => ({ ...i })),
                statusEffects: [...char.statusEffects],
            };
        }
        function getPhaseText(phase) {
            const texts = {
                playerInput: '',
                processing: '',
                enemyAction: '',
                victory: '',
                defeat: '',
                climaxResolution: '',
            };
            return texts[phase];
        }
        function isSkillDisabled(skill) {
            return skill.currentCooldown > 0 || player.value.stats.currentEndurance < skill.cost;
        }
        // =================  =================
        function determineEnemyIntention() {
            // 
            //  . 
            if (enemy.value.skills.length === 0) {
                console.warn('[] ');
                turnState.enemyIntention = null;
                return;
            }
            // ID
            const validSkills = enemy.value.skills.filter(s => {
                // ID
                if (!s.id || !s.name) {
                    console.warn('[] :', s);
                    return false;
                }
                return true;
            });
            if (validSkills.length === 0) {
                console.warn('[] ');
                turnState.enemyIntention = null;
                return;
            }
            // 
            const availableSkills = validSkills.filter(s => s.currentCooldown === 0);
            const skillsToChoose = availableSkills.length > 0 ? availableSkills : validSkills;
            if (skillsToChoose.length === 0) {
                console.warn('[] ');
                turnState.enemyIntention = null;
                return;
            }
            const skill = skillsToChoose[Math.floor(Math.random() * skillsToChoose.length)];
            console.info('[] :', skill.name, 'ID:', skill.id);
            turnState.enemyIntention = skill;
        }
        function handlePlayerSkill(skill) {
            if (turnState.phase !== 'playerInput')
                return;
            // 
            if (playerBoundTurns.value > 0) {
                addLog(`${player.value.name} `, 'system', 'warn');
                return;
            }
            // 
            if (player.value.stats.currentEndurance < skill.cost) {
                addLog('', 'system', 'warn');
                return;
            }
            if (isSkillDisabled(skill)) {
                addLog(skill.currentCooldown > 0 ? '' : '', 'system', 'info');
                return;
            }
            turnState.phase = 'processing';
            const nextPlayer = cloneCharacter(player.value);
            const nextEnemy = cloneCharacter(enemy.value);
            // 
            nextPlayer.stats.currentEndurance -= skill.cost;
            addLog(`${nextPlayer.name}  ${skill.cost} `, 'system', 'info');
            // 
            const skillIndex = nextPlayer.skills.findIndex(s => s.id === skill.id);
            if (skillIndex !== -1) {
                nextPlayer.skills[skillIndex].currentCooldown = skill.cooldown;
                if (skill.cooldown > 0) {
                    addLog(`${skill.name}  ${skill.cooldown} `, 'system', 'info');
                }
            }
            // 
            import('./combatCalculator').then(async ({ executeAttack, applySkillBuffs }) => {
                try {
                    // 
                    if (!skill.data) {
                        addLog(` ${skill.name} `, 'system', 'error');
                        turnState.phase = 'playerInput';
                        return;
                    }
                    const result = executeAttack(nextPlayer, nextEnemy, skill.data);
                    // 
                    addLog(`${nextPlayer.name}  ${skill.name}`, 'player', 'info');
                    if (result.isDodged) {
                        addLog(`${nextEnemy.name} `, 'system', 'info');
                        triggerEffect('dodge');
                    }
                    else {
                        // 
                        console.info('[]  - result.logs:', result.logs);
                        if (result.logs && result.logs.length > 0) {
                            result.logs.forEach(log => {
                                addLog(log, 'system', 'info');
                            });
                        }
                        else {
                            console.warn('[]  - result.logs ');
                        }
                        if (result.isCritical) {
                            addLog(` ${result.actualDamage} `, 'player', 'critical');
                            triggerEffect('critical');
                        }
                        else {
                            addLog(` ${result.actualDamage} `, 'player', 'damage');
                        }
                        // 
                        const oldPleasure = nextEnemy.stats.currentPleasure;
                        nextEnemy.stats.currentPleasure = Math.min(nextEnemy.stats.maxPleasure, nextEnemy.stats.currentPleasure + result.actualDamage);
                        addLog(`${nextEnemy.name}  ${oldPleasure}  ${nextEnemy.stats.currentPleasure}`, 'system', 'info');
                        // buffMVU- 
                        try {
                            const effectLogs = await applySkillEffectsFromMvu(skill.id, true);
                            effectLogs.forEach(log => addLog(log, 'system', 'info'));
                        }
                        catch (e) {
                            console.error('[] ', e);
                            addLog('', 'system', 'warn');
                        }
                    }
                    // 
                    player.value = nextPlayer;
                    enemy.value = nextEnemy;
                    // MVUUI
                    await reloadStatusFromMvu();
                    // 
                    saveToMvu();
                    // 
                    if (nextEnemy.stats.currentPleasure >= nextEnemy.stats.maxPleasure && turnState.climaxTarget === null) {
                        addLog(`${nextEnemy.name} `, 'system', 'critical');
                        // 
                        addLog(`${nextEnemy.name}  ()`, 'system', 'info');
                        triggerEffect('climax');
                        await processClimaxAfterLLM(true);
                    }
                    else {
                        // 
                        addLog(`---  ${nextEnemy.name}  ---`, 'system', 'info');
                        setTimeout(handleEnemyTurn, 1000);
                    }
                }
                catch (e) {
                    console.error('[] ', e);
                    addLog('', 'system', 'error');
                    turnState.phase = 'playerInput';
                }
            });
        }
        async function handlePlayerItem(item) {
            if (turnState.phase !== 'playerInput' || item.quantity <= 0) {
                if (item.quantity <= 0) {
                    addLog(`${item.name} `, 'system', 'info');
                }
                return;
            }
            // processing
            const nextPlayer = cloneCharacter(player.value);
            const nextEnemy = cloneCharacter(enemy.value);
            const itemIndex = nextPlayer.items.findIndex(i => i.id === item.id);
            if (itemIndex > -1) {
                nextPlayer.items[itemIndex].quantity -= 1;
                addLog(`${nextPlayer.name}  ${item.name}${nextPlayer.items[itemIndex].quantity}`, 'system', 'info');
            }
            // 
            const log = item.effect(nextPlayer, nextEnemy);
            addLog(log.message, log.source, log.type);
            // 
            if (item.staminaRestore) {
                addLog(`${nextPlayer.name} ${player.value.stats.currentEndurance}  ${nextPlayer.stats.currentEndurance}`, 'system', 'info');
            }
            if (item.pleasureReduce) {
                addLog(`${nextPlayer.name} ${player.value.stats.currentPleasure}  ${nextPlayer.stats.currentPleasure}`, 'system', 'info');
            }
            // 
            player.value = nextPlayer;
            enemy.value = nextEnemy;
            // MVUUI
            await reloadStatusFromMvu();
            // 
            activeMenu.value = 'main';
            // MVU
            saveToMvu();
            addLog(`---  ${nextPlayer.name}  ---`, 'system', 'info');
            // 
            // 
        }
        function handleEnemyTurn() {
            turnState.phase = 'enemyAction';
            // 
            if (enemyBoundTurns.value > 0) {
                addLog(`${enemy.value.name} `, 'system', 'warn');
                setTimeout(startNewTurn, 1000);
                return;
            }
            addLog(`${enemy.value.name} ...`, 'system', 'info');
            setTimeout(() => {
                // 
                if (!turnState.enemyIntention) {
                    // 
                    determineEnemyIntention();
                    addLog(`${enemy.value.name} `, 'system', 'info');
                }
                const nextPlayer = cloneCharacter(player.value);
                const nextEnemy = cloneCharacter(enemy.value);
                // 
                let skill = nextEnemy.skills.find(s => s.id === turnState.enemyIntention?.id);
                // 
                if (!skill || skill.currentCooldown > 0) {
                    const availableSkills = nextEnemy.skills.filter(s => s.currentCooldown === 0);
                    if (availableSkills.length > 0) {
                        skill = availableSkills[Math.floor(Math.random() * availableSkills.length)];
                        addLog(`${nextEnemy.name}  ${skill.name}`, 'system', 'info');
                    }
                    else {
                        // 
                        skill = nextEnemy.skills[0];
                        addLog(`${nextEnemy.name}  ${skill.name}`, 'system', 'info');
                    }
                }
                if (!skill) {
                    addLog(`${nextEnemy.name} `, 'system', 'info');
                    setTimeout(startNewTurn, 1000);
                    return;
                }
                // 
                const skillCost = skill.data?.staminaCost || skill.cost || 0;
                if (nextEnemy.stats.currentEndurance < skillCost) {
                    addLog(`${nextEnemy.name}  ${skill.name}`, 'system', 'warn');
                    setTimeout(startNewTurn, 1000);
                    return;
                }
                // nullMVU
                if (!skill.data) {
                    try {
                        if (typeof Mvu !== 'undefined') {
                            const mvuData = Mvu.getMvuData({ type: 'message', message_id: 'latest' });
                            if (mvuData) {
                                const mvuSkill = _.get(mvuData.stat_data, `..${skill.id}`, null);
                                if (mvuSkill && mvuSkill.) {
                                    // MVUSkillData
                                    const damageInfo = mvuSkill. || {};
                                    // 
                                    const damageSource = damageInfo. || '';
                                    const coefficient = (damageInfo. || 100) / 100; // 100% = 1.0
                                    // DamageSource
                                    let source;
                                    switch (damageSource) {
                                        case '':
                                            source = 'sex_power'; // DamageSource.SEX_POWER
                                            break;
                                        case '':
                                            source = 'charm'; // DamageSource.CHARM
                                            break;
                                        case '':
                                            source = 'luck'; // DamageSource.LUCK
                                            break;
                                        case '':
                                            source = 'willpower'; // DamageSource.WILLPOWER
                                            break;
                                        case '':
                                            source = 'fixed'; // DamageSource.FIXED
                                            break;
                                        default:
                                            source = 'sex_power';
                                    }
                                    // 
                                    const damageFormula = [
                                        {
                                            source: source,
                                            coefficient: coefficient,
                                            baseValue: 0,
                                        },
                                    ];
                                    const basicSkillData = {
                                        id: skill.id,
                                        name: mvuSkill.. || skill.id,
                                        description: mvuSkill.. || '',
                                        effectDescription: '',
                                        type: 'attack',
                                        staminaCost: mvuSkill.?. || 0,
                                        cooldown: mvuSkill.?. || 0,
                                        castTime: 0,
                                        damageFormula: damageFormula,
                                        accuracy: damageInfo. || 100,
                                        critModifier: 0,
                                        buffs: [],
                                        canBeReflected: false,
                                        hitCount: 1,
                                    };
                                    skill.data = basicSkillData;
