        }
        function startNewTurn() {
            turnState.currentTurn++;
            addLog(`---  ${turnState.currentTurn}  ---`, 'system', 'info');
            turnState.phase = 'playerInput';
            // 
            determineEnemyIntention();
            if (turnState.enemyIntention) {
                addLog(`${enemy.value.name}  ${turnState.enemyIntention.name}`, 'system', 'info');
            }
            // 10
            const oldPlayerEndurance = player.value.stats.currentEndurance;
            player.value.stats.currentEndurance = Math.min(player.value.stats.maxEndurance, player.value.stats.currentEndurance + 10);
            if (player.value.stats.currentEndurance > oldPlayerEndurance) {
                addLog(`${player.value.name}  ${player.value.stats.currentEndurance - oldPlayerEndurance} `, 'system', 'info');
            }
            const oldEnemyEndurance = enemy.value.stats.currentEndurance;
            enemy.value.stats.currentEndurance = Math.min(enemy.value.stats.maxEndurance, enemy.value.stats.currentEndurance + 10);
            if (enemy.value.stats.currentEndurance > oldEnemyEndurance) {
                addLog(`${enemy.value.name}  ${enemy.value.stats.currentEndurance - oldEnemyEndurance} `, 'system', 'info');
            }
            // 
            player.value.skills.forEach(s => {
                if (s.currentCooldown > 0) {
                    s.currentCooldown--;
                    if (s.currentCooldown === 0) {
                        addLog(`${s.name} `, 'system', 'info');
                    }
                }
            });
            enemy.value.skills.forEach(s => {
                if (s.currentCooldown > 0) {
                    s.currentCooldown--;
                }
            });
            // MVU
            updateStatusEffectsFromMvu().then(async (logs) => {
                logs.forEach(log => addLog(log, 'system', 'info'));
                // MVUUI
                await reloadStatusFromMvu();
            });
            addLog(`--- ${player.value.name}  ---`, 'system', 'info');
            saveToMvu();
        }
        // 
        function collectCombatLogs() {
            const filteredLogs = logs.value.filter(log => {
                const message = log.message;
                // 
                if ((log.source === 'player' || log.source === 'enemy') && message.includes('')) {
                    return true;
                }
                // 
                if (message.includes('') && (message.includes('') || message.includes(''))) {
                    return true;
                }
                // 
                if (message.includes('')) {
                    return true;
                }
                // 
                if (message.includes('')) {
                    return true;
                }
                // "XX"
                if (message.includes('') && message.includes('')) {
                    return true;
                }
                // 
                if (message.includes('')) {
                    return true;
                }
                // 
                if (message.includes('')) {
                    return true;
                }
                // 
                if (message.includes('')) {
                    return true;
                }
                // /
                if (message.includes('') || message.includes('') || message.includes('')) {
                    return true;
                }
                // 
                return false;
