            // ""
            const logTexts = filteredLogs.map(log => {
                if (log.source === 'player') {
                    return `${player.value.name}: ${log.message}`;
                }
                else if (log.source === 'enemy') {
                    return `${enemy.value.name}: ${log.message}`;
