        // LLM
        async function sendCombatLogToLLM(context) {
            try {
                const combatLogText = collectCombatLogs();
                const totalTurns = turnState.currentTurn;
                // 
                const isVictory = turnState.phase === 'victory';
                const resultText = isVictory ? '' : '';
                const contextText = isVictory ? '/' : '';
                // 
                const fullPrompt = `${resultText}\n[]\n${combatLogText}\n${totalTurns}\n${contextText}`;
                // 
                if (typeof createChatMessages === 'function') {
                    await createChatMessages([
                        {
                            role: 'user',
                            message: fullPrompt,
                        },
                    ]);
                }
                // LLM
                if (typeof generate === 'function') {
                    addLog('...', 'system', 'info');
                    const generatedText = await generate({ user_input: fullPrompt });
                    // AI
                    if (typeof createChatMessages === 'function') {
                        await createChatMessages([
                            {
                                role: 'assistant',
                                message: `[] ${generatedText}`,
                            },
                        ]);
                        // AI
                        // createChatMessages AI triggerSlash
                        addLog('LLMAI...', 'system', 'info');
                    }
                }
                else {
                    console.warn('[] generate');
                    addLog('generate', 'system', 'warn');
                    //  generate AI
                    if (typeof triggerSlash === 'function') {
                        await triggerSlash('/trigger');
                    }
                }
            }
