        async function processClimaxAfterLLM(targetIsEnemy) {
            // 
            if (turnState.climaxTarget !== null) {
                console.warn('[] ');
                return;
            }
            const char = targetIsEnemy ? enemy.value : player.value;
            // 
            if (char.stats.currentPleasure < char.stats.maxPleasure) {
                console.warn('[] ');
                return;
            }
            // climaxTarget
            turnState.climaxTarget = targetIsEnemy ? 'enemy' : 'player';
            const newChar = cloneCharacter(char);
            newChar.stats.currentPleasure = 0;
            newChar.stats.climaxCount += 1;
            if (targetIsEnemy) {
                enemy.value = newChar;
            }
            else {
                player.value = newChar;
            }
            addLog(`${char.name} ${newChar.stats.climaxCount}/${newChar.stats.maxClimaxCount}`, 'system', 'info');
            // 
            if (newChar.stats.climaxCount >= newChar.stats.maxClimaxCount) {
                turnState.phase = targetIsEnemy ? 'victory' : 'defeat';
                turnState.climaxTarget = null;
                const resultText = targetIsEnemy ? '' : `${turnState.currentTurn}`;
                addLog(targetIsEnemy ? `${resultText}` : `... ${resultText}`, 'system', 'critical');
                // 
                triggerEffect(targetIsEnemy ? 'victory' : 'defeat');
                // 
                await clearTemporaryStatus();
                await initializeCombatSystem();
                saveToMvu();
                return;
            }
            // 
            turnState.climaxTarget = null;
            if (targetIsEnemy) {
                setTimeout(handleEnemyTurn, 500);
            }
            else {
                setTimeout(startNewTurn, 500);
            }
        }
