/**
 * 
 * MVU
 */

export type StatType = 
  | 'sexPower' 
  | 'endurance' 
  | 'evasion' 
  | 'crit' 
  | 'charm' 
  | 'luck' 
  | 'willpower';

/**  - MVU */
export interface CombatStats {
  maxEndurance: number;      // ._
  currentEndurance: number;  // ._
  maxPleasure: number;       // ._
  currentPleasure: number;   // ._
  willpower: number;         // ._
  climaxCount: number;       // .$
  maxClimaxCount: number;    // ..
  
  // 
  sexPower: number;          // .$
  baseEndurance: number;     // .$
  evasion: number;           // .$
  crit: number;              // .$
  charm: number;             // ._
  luck: number;              // ._
  baseWillpower: number;     // .$
}

/**  */
export enum DamageSource {
  SEX_POWER = 'sex_power',
  CHARM = 'charm',
  LUCK = 'luck',
  WILLPOWER = 'willpower',
  FIXED = 'fixed',
}

/**  */
export enum SkillType {
  PHYSICAL = 'physical',
  MENTAL = 'mental',
  CHARM = 'charm',
  SUPPORT = 'support',
  CONTROL = 'control',
  ULTIMATE = 'ultimate',
}

/** Buff */
export enum BuffType {
  ATK_UP = 'atk_up',
  DEF_UP = 'def_up',
  ATK_DOWN = 'atk_down',
  DEF_DOWN = 'def_down',
  SENSITIVE = 'sensitive',
  WILLPOWER_DOWN = 'willpower_down',
  SILENCE = 'silence',
  BIND = 'bind',
  DODGE_DOWN = 'dodge_down',
  CRIT_UP = 'crit_up',
  FOCUS = 'focus',
  SHAME = 'shame',
  HEAT = 'heat',
  FEAR = 'fear',
  DOT_LUST = 'dot_lust',
  REGEN = 'regen',
  ENDURANCE_UP = 'endurance_up',
}

/** Buff */
export interface BuffEffect {
  type: BuffType;
  value: number;
  isPercent: boolean;
  duration: number;
  stackable: boolean;
  maxStacks?: number;
}

/**  */
export interface DamageComponent {
  source: DamageSource;
  coefficient: number;
  baseValue: number;
}

/**  */
export interface SkillData {
  id: string;
  name: string;
  description: string;
  effectDescription: string;
  icon?: string;
  type: SkillType;
  staminaCost: number;
  cooldown: number;
  castTime: number;
  damageFormula: DamageComponent[];
  accuracy: number;
  critModifier: number;
  buffs: BuffEffect[];
  canBeReflected: boolean;
  hitCount: number;
  voiceLine?: string;
}

/**  () */
export interface Skill {
  id: string;
  name: string;
  description: string;
  cost: number;
  type: SkillType;
  cooldown: number;
  currentCooldown: number;
  data: SkillData;
}

/**  */
export interface Item {
  id: string;
  name: string;
  description: string;
  quantity: number;
  effect: (user: Character, target: Character) => CombatLogEntry;
  // 
  staminaRestore?: number;
  pleasureReduce?: number;
}

/**  */
export interface Character {
  id: string;
  name: string;
  avatarUrl: string;
  stats: CombatStats;
  skills: Skill[];
  items: Item[];
  isPlayer: boolean;
  statusEffects: StatusEffect[];
}

/**  () */
export interface StatusEffect {
  id: string;
  name: string;
  duration: number;
  icon: string;
  effect: BuffEffect;
  type: 'buff' | 'debuff';
}

/**  */
export interface CombatLogEntry {
  id: string;
  turn: number;
  message: string;
  source: string;
  type: 'damage' | 'heal' | 'info' | 'critical';
}

/**  */
export interface TurnState {
  currentTurn: number;
  phase: 'playerInput' | 'processing' | 'enemyAction' | 'victory' | 'defeat' | 'climaxResolution';
  enemyIntention: Skill | null;
  climaxTarget: 'player' | 'enemy' | null;
}

